import React from 'react';
import TrainerCover from './TrainerCover/TrainerCover';

export default class Trainers extends React.Component {
    
    constructor() {
        super();
        this.state = { trainersData: [] };
    }

    componentDidMount() {
        fetch('/rest/profiles')
            .then(res => res.json())
            .then(trainersData => this.setState( { trainersData } ))
    }

    render() {
        let trainers = this.state.trainersData;
        return (
            <div className='container'>
                {
                    trainers.map(trainers => (
                        <TrainerCover 
                            key={trainers.id}
                            id={trainers.id}
                            firstname={trainers.firstname}
                            lastname={trainers.lastname}
                            src = {trainers.src}
                            alt = {trainers.lastname}
                        />
                    ))
                }
          </div>
        );
    }
    
}

